.infinite-loader {
  height: 5px;
  width: 100%;
  position: relative;
  overflow: hidden;
  background-color: #cde8f5; }

.infinite-loader:before {
  display: block;
  position: absolute;
  content: "";
  left: -200px;
  width: 200px;
  height: 5px;
  background-color: #048ccd;
  animation: loading 2s linear infinite; }

@keyframes loading {
  from {
    left: -200px;
    width: 30%; }
  50% {
    width: 30%; }
  70% {
    width: 70%; }
  80% {
    left: 50%; }
  95% {
    left: 120%; }
  to {
    left: 100%; } }

@media screen and (min-width: 768px) {
  .infinite-loader {
    position: absolute;
    bottom: 0;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px; } }
